<template>
  <div class="hello">
    <a-tabs @change="callback" size="large">
      <a-tab-pane key="1" tab="">
        <div class="form">
          <h5></h5>
          <div class="form_data">
            <h6></h6>
            <a-input v-model:value="information.nickname" />
          </div>
          <div class="form_data">
            <h6></h6>
            <a-cascader
              :options="map"
              placeholder=""
              @change="onChange"
              size="large"
              class="cascader"
            />
          </div>
          <div class="form_data">
            <h6></h6>
            <a-input v-model:value="information.phone" />
          </div>
          <div class="form_data">
            <h6></h6>
            <a-input v-model:value="information.idcard" />
          </div>
          <div class="form_data">
            <h6></h6>
            <a-radio-group
              name="radioGroup"
              v-model:value="information.is_poor"
            >
              <a-radio value="1">  </a-radio>
              <a-radio value="0">  </a-radio>
            </a-radio-group>
          </div>
          <div class="form_data">
            <h6></h6>
            <a-select
              v-model:value="information.credit_rating"
              style="width: 100%"
              ref="select"
            >
              <a-select-option
                v-for="(item, i) in creditlist"
                :key="i"
                :value="item.name"
              >
                {{ item.name }}
              </a-select-option>
            </a-select>
          </div>
          <div class="form_data">
            <h6></h6>
            <a-date-picker
              v-model:value="information.loan_starttime"
              @change="information_starttime"
            />
          </div>
          <div class="form_data">
            <h6></h6>
            <a-date-picker
              v-model:value="information.loan_endtime"
              @change="information_endtime"
            />
          </div>
          <div class="form_data">
            <h6></h6>
            <a-date-picker
              v-model:value="information.payback_time"
              @change="information_payback"
            />
          </div>
          <div class="form_data">
            <h6></h6>
            <a-radio-group
              name="radioGroup"
              v-model:value="information.loan_status_type"
            >
              <a-radio value="">  </a-radio>
              <a-radio value="">  </a-radio>
              <a-radio value="">  </a-radio>
            </a-radio-group>
          </div>
          <div class="form_data" v-if="ifoverduereason_information == true">
            <h6></h6>
            <a-radio-group
              :style="radioStyle"
              :options="overduereasonlist"
              v-model:value="information.overduereason"
            />
          </div>
          <div class="form_data">
            <h6></h6>
            <a-input
              v-model:value="information.loan_price"
              placeholder=""
            />
          </div>
          <div class="form_data">
            <h6></h6>
            <a-input v-model:value="information.year_interest_rate" />
          </div>
          <div class="form_data">
            <h6></h6>
            <a-input v-model:value="information.discount_rate" placeholder="" />
          </div>
          <div class="form_data">
            <h6>()</h6>
            <a-input v-model:value="information.discount_price" />
          </div>
          <div class="form_data">
            <h6>()</h6>
            <a-input v-model:value="information.supportprice" />
          </div>
          <div class="form_data">
            <h6></h6>
            <a-cascader
              :options="industry"
              placeholder=""
              @change="industrybtn"
              size="large"
              class="cascader"
            />
          </div>
          <div class="form_data">
            <h6></h6>
            <a-radio-group
              name="radioGroup"
              v-model:value="information.loan_type"
            >
              <a-radio value="">  </a-radio>
              <a-radio value="">  </a-radio>
            </a-radio-group>
          </div>
          <div class="form_data">
            <h6></h6>
            <a-select
              v-model:value="information.loan_bank"
              style="width: 100%"
              ref="select"
            >
              <a-select-option
                v-for="(item, i) in Loan_banklist"
                :key="i"
                :value="item.bank_name"
              >
                {{ item.bank_name }}
              </a-select-option>
            </a-select>
          </div>
          <div class="form_data">
            <h6></h6>
            <a-textarea
              v-model:value="information.loan_bank_desc"
              :auto-size="{ minRows: 4 }"
            />
          </div>
          <div class="form_data">
            <h6></h6>
            <a-radio-group
              :options="isjoincredit"
              v-model:value="information.is_joincredit"
            />
          </div>
          <div class="form_data">
            <h6></h6>
            <a-radio-group
              name="radioGroup"
              v-model:value="information.is_safe"
            >
              <a-radio value="">  </a-radio>
              <a-radio value="">  </a-radio>
            </a-radio-group>
          </div>
          <div class="form_data">
            <h6></h6>
            <a-upload
              action="http://jinrongapi.yooobei.com/Loan/uploads"
              list-type="picture"
              multiple="true"
              v-model:fileList="fileList"
              @change="handleChange"
            >
              <a-button> <upload-outlined /> upload </a-button>
            </a-upload>
          </div>
          <div class="form_btn">
            <a-button
              @click="primary_personal"
              style="margin-right: 20px"
              type="primary"
              ></a-button
            >
            <a-button @click="Reset"></a-button>
          </div>
        </div>
      </a-tab-pane>
      <a-tab-pane key="2" tab="" force-render>
        <div class="form">
          <h5></h5>
          <div class="form_data">
            <h6></h6>
            <a-cascader
              :options="map"
              placeholder=""
              @change="onChangx"
              size="large"
              class="cascader"
            />
          </div>
          <div class="form_data">
            <h6></h6>
            <a-input v-model:value="enterprise.nickname" />
          </div>
          <div class="form_data">
            <h6></h6>
            <a-input v-model:value="enterprise.credit_code" />
          </div>
          <div class="form_data">
            <h6></h6>
            <a-input v-model:value="enterprise.deputy_username" />
          </div>
          <div class="form_data">
            <h6></h6>
            <a-input v-model:value="enterprise.phone" />
          </div>
          <div class="form_data">
            <h6></h6>
            <a-input v-model:value="enterprise.address" />
          </div>
          <div class="form_data">
            <h6></h6>
            <a-input
              v-model:value="enterprise.staff_num"
              placeholder=""
            />
          </div>
          <div class="form_data">
            <h6></h6>
            <a-radio-group
              name="radioGroup"
              v-model:value="enterprise.is_faucet"
            >
              <a-radio value="0">  </a-radio>
              <a-radio value="1">  </a-radio>
            </a-radio-group>
          </div>
          <div class="form_data">
            <h6></h6>
            <a-input v-model:value="enterprise.loan_price" />
          </div>
          <div class="form_data">
            <h6></h6>
            <a-date-picker
              v-model:value="enterprise.loan_starttime"
              @change="enterprise_starttime"
            />
          </div>
          <div class="form_data">
            <h6></h6>
            <a-date-picker
              v-model:value="enterprise.loan_endtime"
              @change="enterprise_endtime"
            />
          </div>
          <div class="form_data">
            <h6></h6>
            <a-date-picker
              v-model:value="enterprise.payback_time"
              @change="enterprise_payback"
            />
          </div>
          <div class="form_data">
            <h6></h6>
            <a-radio-group
              name="radioGroup"
              v-model:value="enterprise.loan_status_type"
            >
              <a-radio value="">  </a-radio>
              <a-radio value="">  </a-radio>
              <a-radio value="">  </a-radio>
            </a-radio-group>
          </div>
          <div class="form_data" v-if="ifoverduereason_enterprise == true">
            <h6></h6>
            <a-radio-group
              :style="radioStyle"
              :options="overduereasonlist"
              v-model:value="enterprise.overduereason"
            />
          </div>
          <div class="form_data">
            <h6></h6>
            <a-input v-model:value="enterprise.year_interest_rate" />
          </div>
          <div class="form_data">
            <h6></h6>
            <a-input v-model:value="enterprise.discount_rate" placeholder="" />
          </div>
          <div class="form_data">
            <h6>()</h6>
            <a-input v-model:value="enterprise.discount_price" />
          </div>
          <div class="form_data">
            <h6></h6>
            <a-cascader
              :options="industry"
              placeholder=""
              @change="enterprisebtn"
              size="large"
              class="cascader"
            />
          </div>
          <div class="form_data">
            <h6>()</h6>
            <a-input v-model:value="enterprise.supportprice" />
          </div>
          <div class="form_data">
            <h6>()</h6>
            <a-input v-model:value="enterprise.uppoor_num" />
          </div>
          <div class="form_data">
            <h6></h6>
            <a-select
              v-model:value="enterprise.loan_bank"
              style="width: 100%"
              ref="select"
            >
              <a-select-option
                v-for="(item, i) in Loan_banklist"
                :key="i"
                :value="item.bank_name"
              >
                {{ item.bank_name }}
              </a-select-option>
            </a-select>
          </div>
          <div class="form_data">
            <h6></h6>
            <a-textarea
              v-model:value="enterprise.loan_bank_desc"
              :auto-size="{ minRows: 4 }"
            />
          </div>
          <div class="form_data">
            <h6></h6>
            <a-select
              v-model:value="enterprise.credit_rating"
              style="width: 100%"
              ref="select"
            >
              <a-select-option
                v-for="(item, i) in creditlist"
                :key="i"
                :value="item.name"
              >
                {{ item.name }}
              </a-select-option>
            </a-select>
          </div>
          <div class="form_data">
            <h6></h6>
            <a-radio-group
              :options="loanmodel"
              v-model:value="enterprise.loanmodel"
            />
          </div>
          <div class="form_data">
            <h6></h6>
            <a-radio-group
              name="radioGroup"
              v-model:value="enterprise.loan_type"
            >
              <a-radio value="">  </a-radio>
              <a-radio value="">  </a-radio>
            </a-radio-group>
          </div>
          <div class="form_data">
            <h6></h6>
            <a-checkbox-group
              v-model:value="enterprise.uppoor_type"
              :options="mannerlist"
              @change="enterprise_uppoor_type"
            />
          </div>
          <div class="form_data">
            <h6></h6>
            <a-upload
              action="http://jinrongapi.yooobei.com/Loan/uploads"
              list-type="picture"
              multiple="true"
              v-model:fileList="povertyList"
              @change="handlepoverty"
            >
              <a-button> <upload-outlined />  </a-button>
            </a-upload>
          </div>
          <div class="form_data">
            <h6></h6>
            <a-upload
              action="http://jinrongapi.yooobei.com/Loan/uploads"
              list-type="picture"
              multiple="true"
              v-model:fileList="proveList"
              @change="handleprove"
            >
              <a-button> <upload-outlined />  </a-button>
            </a-upload>
          </div>
          <div class="form_btn">
            <a-button
              @click="primary_enterprise"
              style="margin-right: 20px"
              type="primary"
              ></a-button
            >
            <a-button @click="Reset"></a-button>
          </div>
        </div>
      </a-tab-pane>
    </a-tabs>
  </div>
</template>

<script>
import { UploadOutlined } from "@ant-design/icons-vue";
import { message } from "ant-design-vue";
import maplist from "../assets/json/map.json";
import https from "@/utils/https.js";
export default {
  name: "mobile",
  props: {},
  comments: { UploadOutlined },
  data() {
    return {
      title_left: "",
      title_right: "",
      // 
      ifoverduereason_information: false,
      // 
      ifoverduereason_enterprise: false,
      // 
      map: [maplist],
      // 
      creditlist: [
        { name: "AAA+" },
        { name: "AAA" },
        { name: "AA" },
        { name: "A" },
        { name: "" },
      ],
      // 
      overduereasonlist: [],
      // 
      Loan_banklist: [],
      // 
      industry: [],
      // 
      isjoincredit: [],
      // 
      loanmodel: [],
      // 
      mannerlist: [],
      // 
      information: {
        nickname: "", //
        area: "", //
        country: "", //
        village: "", //
        phone: "", //
        idcard: "", //
        is_poor: "1", //
        credit_rating: "AAA+", //
        loan_starttime: "", //
        loan_endtime: "", //
        payback_time: "", //
        loan_status_type: "", //
        overduereason: "", //
        loan_price: "", //
        year_interest_rate: "", //
        discount_rate: "", //
        discount_price: "", //
        supportprice: "", // 
        loan_use: "",
        distribution: "", //
        loan_type: "", //
        loan_bank: "", //
        loan_bank_desc: "", //
        is_joincredit: "", //
        is_safe: "", //
        entrustprove: "", //
      },
      // 
      enterprise: {
        area: "", //
        country: "", //
        village: "", //
        nickname: "", //
        credit_code: "", //
        deputy_username: "", //
        phone: "", //
        address: "", //
        staff_num: "", //
        is_faucet: "0", //
        loan_price: "", //
        loan_starttime: "", //
        loan_endtime: "", //
        payback_time: "", //
        loan_status_type: "", //
        overduereason: "", //
        year_interest_rate: "", //
        discount_rate: "", //
        discount_price: "", //
        loan_use: "",
        distribution: "",
        supportprice: "", // 
        uppoor_num: "", //
        loan_bank: "", //
        loan_bank_desc: "", //
        credit_rating: "AAA+", //
        loanmodel: "", //
        loan_type: "", //
        uppoor_type: "", //
        uppoor_roster: "", //
        entrustprove: "", //
      },
      // 
      fileList: [],
      // 
      povertyList: [],
      proveList: [],
    };
  },
  created() {
    https
      .fetchPost("/Loan/init")
      .then((res) => {
        console.log(res.data);
        if (res.data.code == 200) {
          this.Loan_banklist = res.data.data.banklist;
          this.industry = res.data.data.purposeloan;
          this.industry[0].children = res.data.data.distribution;
          this.isjoincredit = res.data.data.isjoincredit;
          this.loanmodel = res.data.data.loanmodel;
          this.mannerlist = res.data.data.uppoortype;
          this.overduereasonlist = res.data.data.overduereason;

          this.industry.map((item) => (item.label = item.value));
          this.industry[0].children.map((item) => (item.label = item.value));
          this.isjoincredit.map((item) => (item.label = item.value));
          this.loanmodel.map((item) => (item.label = item.value));
          this.mannerlist.map((item) => {
            item.label = item.value;
            item.value = item.key;
          });
          this.overduereasonlist.map((item) => (item.label = item.value));
        }
      })
      .catch((res) => {
        console.log(res);
      });
  },
  methods: {
    // 
    onChange(value, selectedOptions) {
      console.log(value);
      this.information.area = selectedOptions[0].label;
      this.information.country = selectedOptions[1].label;
      this.information.village = selectedOptions[2].label;
    },
    // 
    onChangx(value, selectedOptions) {
      console.log(value);
      this.enterprise.area = selectedOptions[0].label;
      this.enterprise.country = selectedOptions[1].label;
      this.enterprise.village = selectedOptions[2].label;
    },

    // 
    industrybtn(value, selectedOptions) {
      console.log(selectedOptions);
      this.information.loan_use = selectedOptions[0].label;
      if (selectedOptions[1]) {
        this.information.distribution = selectedOptions[1].label;
      }
    },
    // 
    enterprisebtn(value, selectedOptions) {
      console.log(selectedOptions);
      this.enterprise.loan_use = selectedOptions[0].label;
      if (selectedOptions[1]) {
        this.enterprise.distribution = selectedOptions[1].label;
      }
    },

    // 
    enterprise_uppoor_type(e, index) {
      console.log(e);
      console.log(index);
    },

    // 
    information_starttime(date, dateString) {
      this.information.loan_starttime = dateString;
    },
    information_endtime(date, dateString) {
      console.log(date);
      this.information.loan_endtime = dateString;
    },
    information_payback(date, dateString) {
      console.log(date, dateString);
      if (this.information.loan_endtime) {
        if (
          Date.parse(this.information.payback_time) <=
          Date.parse(this.information.loan_endtime)
        ) {
          this.information.payback_time = dateString;
          this.ifoverduereason_information = false;
        } else {
          this.ifoverduereason_information = true;
          this.information.payback_time = dateString;
        }
      } else {
        this.information.payback_time = "";
        message.info("");
      }
    },

    enterprise_starttime(date, dateString) {
      this.enterprise.loan_starttime = dateString;
    },
    enterprise_endtime(date, dateString) {
      this.enterprise.loan_endtime = dateString;
    },
    enterprise_payback(date, dateString) {
      console.log(date, dateString);
      if (this.enterprise.loan_endtime) {
        if (
          Date.parse(this.enterprise.payback_time) <=
          Date.parse(this.enterprise.loan_endtime)
        ) {
          this.enterprise.payback_time = dateString;
          this.ifoverduereason_enterprise = false;
        } else {
          this.ifoverduereason_enterprise = true;
          this.enterprise.payback_time = dateString;
        }
      } else {
        this.enterprise.payback_time = "";
        message.info("");
      }
    },

    // 
    primary_personal() {
      console.log(this.information);
      https
        .fetchPost("/Loan/userInsertData", this.information)
        .then((res) => {
          console.log(res);
          if (res.data.code == 200) message.success("");
          else message.error(res.data.msg);
        })
        .catch((res) => {
          console.log(res);
        });
    },
    primary_enterprise() {
      // message.info();
      let uppoor_type = "";
      this.enterprise.uppoor_type.map(
        (item) => (uppoor_type = uppoor_type + item + ",")
      );
      this.enterprise.uppoor_type = uppoor_type;
      console.log(this.enterprise);

      https
        .fetchPost("/Loan/enterInsertData", this.enterprise)
        .then((res) => {
          console.log(res);
          if (res.data.code == 200) message.success("");
          else message.error(res.data.msg);
        })
        .catch((res) => {
          console.log(res);
        });
    },

    // 
    Reset() {
      // 
      this.ifoverduereason_information = false;
      // 
      this.ifoverduereason_enterprise = false;
      // 
      this.information = {
        nickname: "", //
        area: "", //
        country: "", //
        village: "", //
        phone: "", //
        idcard: "", //
        is_poor: "1", //
        credit_rating: "AAA+", //
        loan_starttime: "", //
        loan_endtime: "", //
        payback_time: "", //
        loan_status_type: "", //
        overduereason: "", //
        loan_price: "", //
        year_interest_rate: "", //
        discount_rate: "", //
        discount_price: "", //
        supportprice: "", // 
        loan_use: "",
        distribution: "", //
        loan_type: "", //
        loan_bank: "", //
        loan_bank_desc: "", //
        is_joincredit: "", //
        is_safe: "", //
        entrustprove: "", //
      };
      // 
      this.enterprise = {
        area: "", //
        country: "", //
        village: "", //
        nickname: "", //
        credit_code: "", //
        deputy_username: "", //
        phone: "", //
        address: "", //
        staff_num: "", //
        is_faucet: "0", //
        loan_price: "", //
        loan_starttime: "", //
        loan_endtime: "", //
        payback_time: "", //
        loan_status_type: "", //
        overduereason: "", //
        year_interest_rate: "", //
        discount_rate: "", //
        discount_price: "", //
        loan_use: "",
        distribution: "",
        supportprice: "", // 
        uppoor_num: "", //
        loan_bank: "", //
        loan_bank_desc: "", //
        credit_rating: "AAA+", //
        loanmodel: "", //
        loan_type: "", //
        uppoor_type: "", //
        uppoor_roster: "", //
        entrustprove: "", //
      };
      // 
      this.fileList = [];
      // 
      this.povertyList = [];
      this.proveList = [];
    },

    // 
    handleChange(info) {
      if (info.file.status !== "done") return;
      console.log(info.fileList[0].response.code);

      if (info.fileList[0].response.code == 200) {
        message.info("");
        this.information.entrustprove =
          this.information.entrustprove + info.fileList[0].response.data + ",";
      }
    },

    // 
    handlepoverty(info) {
      if (info.file.status !== "done") return;
      console.log(info.fileList[0].response.code);

      if (info.fileList[0].response.code == 200) {
        message.info("");
        this.enterprise.uppoor_roster =
          this.enterprise.uppoor_roster + info.fileList[0].response.data + ",";
      }
    },

    // 
    handleprove(info) {
      if (info.file.status !== "done") return;
      console.log(info.fileList[0].response.code);

      if (info.fileList[0].response.code == 200) {
        message.info("");
        this.enterprise.entrustprove =
          this.enterprise.entrustprove + info.fileList[0].response.data + ",";
      }
    },
  },
};
</script>

<style scoped lang="scss">
.hello {
  width: 100%;

  .form {
    padding: 0 10px 20px 10px;

    h5 {
      text-align: center;
      font-size: 26px;
    }

    .form_data {
      margin-top: 10px;

      .cascader {
        width: 335px;
      }
      .radioStyle {
        display: block;
      }
    }

    .form_btn {
      margin-top: 20px;
    }
  }
}
</style>